@font-face {
   font-family: 'Titre';
   src: url("../fonts/Viaoda_Libre/ViaodaLibre-Regular.ttf");
}

@font-face {
   font-family: 'Titre2';
   src: url("../fonts/Dancing_Script/DancingScript-VariableFont_wght.ttf");
}

@font-face {
   font-family: 'Text-medium';
   src: url("../fonts/Roboto/Roboto-Medium.ttf");
}

@font-face {
   font-family: 'Text-regular';
   src: url("../fonts/Roboto/Roboto-Regular.ttf");
}

body{
  margin:0;
  padding: 0;
  font-size: 1.2em;
  font-family: 'Text-regular';
}

.navbar.hidden, .main.hidden{
  opacity: 0;
}

.loader{
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: teal;


  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  z-index: 100;
}

.loader.hidden{
  display: none;
}
